.comment {
  width: 280px;
  margin-bottom: 20px;
  padding-left: 17px;
  padding-top: 25px;
  padding-right: 31px;
  padding-bottom: 28px;
  color: $main-color;
  background-color: $light-grey;
}

.comment--view-of-day {
  margin-left: 20px;
  margin-right: 20px;
}

.comment__author {
  font-family: "OpenSans Bold", sans-serif;
  font-size: 14px;
}

.comment__time {
  font-family: "OpenSans Light", sans-serif;
  font-size: 12px;
}

.comment__description {
  margin: 0;
  font-size: 14px;
  line-height: 1.71;
}

.comment__likes {
  margin: 0;
  font-size: 12px;
  color: $pink;
}
